<template>
  <div>
    <transition name="asid">
      <div @mousemove="drawStar" v-if="show" class="asid">
        <canvas :ref="'canvas'" id="canvas" class="canvas"> </canvas>
        <ul class="button-container">
          <get-start :color="'colorful'"> </get-start>
        </ul>
        <ul class="contact-container">
          <icon-button :title="'github'" :icon="'icon-github'"></icon-button>
          <icon-button :title="'qq'" :icon="'icon-qq-copy'"></icon-button>
          <icon-button :title="'wechat'" :icon="'icon-wechat'"></icon-button>
        </ul>
        <ul class="introduce-container">
          <li class="info author">
            {{ info.nickname }}
          </li>
          <li
            class="info motor"
            v-for="(motorword, index) in info.motor.split('')"
            :key="index"
          >
            {{ motorword }}
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "asid",
  data() {
    return {
      info: {
        nickname: "咸鱼润",
        motor: "一个把咸鱼作为目标的孩子",
      },
      show: true,
    };
  },
  methods: {
     drawStar(e){
         let canvas = this.$refs.canvas,
        pen = canvas.getContext('2d'),
        current_config={
// 当前配置
          cilentX:null,
          cilentY:null,
          max:20000
        },
        canvas_config={
          // 画布配置
          width:null,
          height:null,
          nums:null
        },
        random_lines_config=[
          // 用于生成每一条线的关系
          /*
          *@params {
            position:{x,y},
            motionDirect:{endX,endY},
            distance
          }
          */ 
        ]
        ,random=()=>Math.random(),
        lines_array = [];
        canvas_config.width = canvas.offsetWidth;
        canvas_config.height = canvas.offsetHeight;
        current_config.cilentX = e.cilentX;
        current_config.cilentY = e.cilentY;
        canvas_config.nums = 20;
        for (let i = 0 ; i<canvas_config.nums ; i++) 
          random_lines_config.push({
            position:{
              x:random() * canvas_config.width,
              y:random() * canvas_config.height
            },
            motionDirect:{
              _x:2*random() - 1,
              _y:2*random() - 1
            },
            max:60//黏附距离
          });
          let current_point ={
            position:{
              x:e.cilentX,
              y:e.cilentY
            },
            max:60//黏附距离
          }
        lines_array = random_lines_config.concat([current_point])
        // 生成随机
        console.log(canvas_config.width)
        function randomDraw(pen,random_lines_config ,all_point){
          let  e,i,d,x_dist,y_dist,dist;
          pen.clearRect(0 , 0 , canvas_config.width , canvas_config.height)
          random_lines_config.forEach(function(r , idx){
            r.position.x +=r.motionDirect._x;
            r.position.y +=r.motionDirect._y;
            r.motionDirect._x *=r.position.x > canvas_config.width || r.position.x<0? -1:1 ;
            r.motionDirect._y *=r.position.y >canvas_config.height || r.position.y<0?-1:1;
            pen.fillRect(r.position.x - 0.5 , r.y-0.5,1,1);
            for (i = idx+1 ; i<all_point.length;i++){
              e = all_point[i];
              if(null !== e.position.x && null !==e.position.y){
                x_dist = r.position.x - e.position.x;
                y_dist = r.position.y - e.position.y;
                dist = x_dist*x_dist + y_dist * y_dist; 
                dist < e.max &&
                 (e.x === current_config.cilentX &&
                 dist >=e.max/2 && 
                (r.position.x -= 0.03 * x_dist,
                r.position.y -= 0.03 *y_dist)),
                d = (e.max - dist)/e.max,
                pen.beginPath(),
                pen.lineWidth = d/4,
                pen.moveTo(r.position.x , r.position.y),
                pen.lineTo(e.position.x , e.position.y),
                pen.stroke();
              }
              
            }
          })

        }

    }
  },
  components: {
    "get-start": () => import("./common-button"),
    "icon-button": () => import("./circle-button"),
  },
};
</script>

<style scoped>
@font-face {
  font-family: introduceFont;
  src: url("../assets/font/introducefont.ttf");
}
.canvas {
  position: absolute;
  background-color: rgb(2, 77, 77);
  z-index: -10;
  width: 20em;
  height: 100%;
}
.button-container {
  padding-top: 14px;
  padding-bottom: 14px;
  width: 200px;
  text-align: center;
}
.contact-container {
  padding-top: 14px;
  padding-bottom: 14px;
  width: 200px;
  text-align: center;
  border-bottom: solid 1px #eee;
}
.iconfont:nth-child(1) {
  font-size: 49px;
}
.iconfont:nth-child(2) {
  font-size: 55px;
}
.iconfont:nth-child(3) {
  font-size: 50px;
}
.info {
  color: #eee;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.asid {
  overflow: hidden;
  position: fixed;
  display: inline-block;
  width: 200px;
  height: 100vh;
}
.author {
  margin: 20px;
  font-family: introduceFont;
  font-size: 4em;
}
.motor {
  list-style: none;
  transition-duration: 2s;
  font-size: 2em;
  font-family: introduceFont;
}

.motor:nth-child(2n + 1) {
  transform: translateX(100px);
}
.motor:nth-child(2n + 2) {
  transform: translateX(40px);
}
</style>



